import model from '../../components/model/model.vue'
import {
	ajax
} from '../../common/js/util.js'
import {
	Toast
} from 'vant'
export default {
	data() {
		return {
			Color: '#DE2910',
			value: '',
			backColor: '#FFFFFF',
			show: false,
			left: 'justify',
			dataList: {},
			id: '',
			serviceList: [],
			refundList: {}
		}
	},
	components: {
		model,
		Toast
	},
	onLoad(option) {
		//刷新保留用户uid
		if (this.getRequest('uid')) {
			var uids = this.getRequest('uid')
			this.$store.commit('changeUid', uids)
		}
		this.id = option.id
		this.init()
	},
	onReady() {

	},
	methods: {
		getRequest(variable) {
			var query = window.location.search.substring(1);
			var vars = query.split("&");
			for (var i = 0; i < vars.length; i++) {
				var pair = vars[i].split("=");
				if (pair[0] == variable) {
					return pair[1];
				}
			}
			return (false);
		},
		init() {
			let self = this
			let datas = {
				uid: self.$store.state.uid,
				orderId: self.id
			}
			let data = {
				url: '/api/gzh/viphallOrderDetail',
				data: datas,
				success: function(res) {
					console.log(res)
					if (res.data.result == 0) {
						self.dataList = res.data
						self.serviceList = res.data.assistantList
						self.refundList = res.data.refund
					}
				}
			}
			ajax(data)
		},
		cancel() {
			this.show = false
		},
		confirm() {
			this.show = false
			let self = this
			let datas = {
				uid: self.$store.state.uid,
				orderId: self.id
			}
			let data = {
				url: '/api/gzh/cancelViphallOrder',
				data: datas,
				success: function(res) {
					console.log(res)
					if (res.data.result == 0) {
						Toast('取消成功')
						setTimeout(() => {
							uni.navigateBack({
								delta: 1
							});
						}, 500)
					}
				}
			}
			ajax(data)
		},
		open() {
			this.show = true
		},
		goService() {
			// uni.navigateTo({
			// 	url: '../assistant/assistant?list=' + JSON.stringify(this.serviceList)
			// })
			let self = this
			uni.makePhoneCall({
				phoneNumber: self.$store.state.customer
			});
		}
	}
}
